import {
  GraphQLObjectType,
  GraphQLNonNull,
  GraphQLSchema,
  GraphQLString,
  GraphQLList
} from 'graphql/type';

import * as config from '../../config';

const courseInfo = {
  type: GraphQLObjectType,
  resolve({ request }) {
    return {
      result: {
        code: 1,
        courseInfo: config.courseInfo
      }
    };
  }
};

export default courseInfo;
